<template>
  <div class="store">
    <div class="top">
      <div class="button-wrapper">
        <button @click="showDraw">显示抽屉</button>
        <button @click="showWidth">显示左右</button>
        <button @click="switchHeight">切换高度</button>
      </div>
    </div>
    <div class="content-wrapper">
      <div class="left" :class="{hide:hideLeft}">
        <div class="left-item">第一条</div>
        <div class="left-item">第二条</div>
      </div>
      <div class="middle" :class="{zeroH:zeroHeight}">
        <div @click="switchHeight">{{count}}</div>
        <div>oneFirstState: {{oneFirstState}}</div>
        <div>twoFirstState: {{twoFirstState}}</div>
        <div @click="add({mount:2})">add(M)</div>
        <div @click="reduce({mount:5})">reduce(M)</div>
        <div @click="increase({mount:50})">increase(A)</div>
        <div @click="oneFirstAction(params)">oneFirstAction(one)</div>
        <i class="iconfont icon-left" :class="{reverse:hideLeft}" @click="toggleWidth('left')"></i>
        <i class="iconfont icon-right" :class="{reverse:hideRight}" @click="toggleWidth"></i>
      </div>
      <div class="right" :class="{hide:hideRight}">
        <div class="right-item">提一条</div>
        <div class="right-item">提二条</div>
      </div>
    </div>
    <div class="drawer" :class="{active:drawState}">
      <div class="draw-item" @click="showDraw">抽屉</div>
      <!-- <div class="draw-item">抽屉</div> -->
      <!-- <div class="draw-item">抽屉</div> -->
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState, mapActions } from 'vuex'
export default {
  data() {
    return {
      params: {
        msg:'来自one模块',
        mount: 100
      },
      drawState: false,
      hideLeft: false,
      hideRight: true,
      zeroHeight: false
    }
  },
  computed: {
    ...mapState([
      'count'
    ]),
    ...mapState('one', [
      'oneFirstState'
    ]),
    ...mapState('two', [
      'twoFirstState'
    ])
  },
  methods: {
    ...mapMutations([
      'add', 'reduce'
    ]),
    ...mapActions([
      'increase'
    ]),
    ...mapActions('one', [
      'oneFirstAction'
    ]),
    showDraw() {
      this.drawState = !this.drawState
    },
    toggleWidth(flag) {
      flag === 'left' ? (this.hideLeft = !this.hideLeft) : (this.hideRight = !this.hideRight)
    },
    showWidth() {
      this.hideLeft = !this.hideLeft
      this.hideRight = !this.hideRight
    },
    switchHeight() {
      this.zeroHeight = !this.zeroHeight
    }
  }
}
</script>

